<template>
    <div>
        <div
            v-for="item in nav"
            :key="item.index"
            @contextmenu.prevent="contain.handleContextMenu && contain.handleContextMenu($event, item)"
        >
            <avue-draggable
                v-if="!item.children"
                v-bind="item"
                :scale="container.stepScale"
                :disabled="!contain.menuFlag"
                :step="container.stepScale"
                :width="item.component.width"
                :height="item.component.height"
                :ref="common.DEAFNAME + item.index"
                :id="common.DEAFNAME + item.index"
                :active-flag="contain.active.includes(item.index)"
                v-show="!item.display"
                @over="handleOver"
                @focus="handleFocus"
                @blur="handleBlur"
            >
                <component
                    :ref="common.NAME + item.index"
                    :id="common.NAME + item.index"
                    :is="common.COMPNAME + item.component.name"
                    v-bind="item"
                    :data-formatter="getFunction(item.dataFormatter)"
                    :click-formatter="getFunction(item.clickFormatter, true)"
                    :echart-formatter="getFunction(item.echartFormatter)"
                    :label-formatter="getFunction(item.labelFormatter)"
                    :formatter="getFunction(item.formatter)"
                    :width="item.component.width"
                    :data-query="getJson(item.dataQuery)"
                    :height="item.component.height"
                    :animation="!contain.menuFlag"
                    :theme="(item.option || {}).theme"
                    :disabled="!contain.menuFlag"
                    :scale="container.stepScale"
                    :option="item.option"
                    :home-url="contain.config.url"
                    title=""
                    :click="handleClick"
                />
            </avue-draggable>
            <subgroup :nav="item.children"></subgroup>
        </div>
    </div>
</template>

<script>
import common from "@/config";
export default {
    name: "subgroup",
    inject: ["contain", "container"],
    provide() {
        return {
            contain: this.contain,
            container: this.container,
        };
    },
    props: {
        nav: {
            type: Array,
            default: () => {
                return [];
            },
        },
    },
    data() {
        return {
            common: common,
        };
    },
    methods: {
        getFunction(fun, def) {
            if (!this.validatenull(fun)) {
                try {
                    return eval(fun);
                } catch {
                    return () => {};
                }
            }
            if (def) return () => {};
        },
        getJson(str) {
            if (this.validatenull(str)) return {};
            if (typeof str == "string") {
                try {
                    return JSON.parse(str);
                } catch {
                    return {};
                }
            }
            return str;
        },
        //点击事件交互
        handleClick({ type, child, value }) {
            if (type === "tabs") {
                const indexList = child.index;
                indexList.forEach(index => {
                    const paramName = child.paramName.split(',');
                    const item = this.contain.findlist(index);
                    if (item.component.name === "iframe") {
                        if (item.data.value) {
                            const url = item.data.value.split("?");
                            let str = '';
                            if(url.length === 1){
                                paramName.forEach(param => {
                                    if(typeof value === 'object'){
                                        str += str === '' ? `${param}=${value[param]}` : `&${param}=${value[param]}`
                                    }
                                    if(typeof value === 'string'){
                                        str += str === '' ? `${param}=${value}` : `&${param}=${value}`
                                    }
                                })
                            }
                            if(url.length === 2){
                                const params = url[1].split("&");
                                let opts = {};
                                params.forEach(item => {
                                    const p = item.split("=");
                                    opts[p[0]] = p[1]
                                })
                                if(typeof value === 'object'){
                                    opts = {...opts,...value}
                                }
                                if(typeof value === 'string' || typeof value === 'number'){
                                    for(let k in opts){
                                        const i = paramName.findIndex(o => o === k)
                                        if(i >= 0){
                                            opts[k] = value
                                        }
                                    }
                                }
                                for(let k in opts){
                                    str += `${(str !== '' ? '&' : '') + k}=${opts[k]}`
                                }
                            }
                            item.data.value = `${url[0]}?${str}`
                        }
                    }else{
                        let params = {};
                        if (item.dataQuery) {
                            params = this.getJson(item.dataQuery);
                        } else {
                            params = {};
                        }
                        if(typeof value === 'object'){
                            params = {...params, ...value}
                        }else{
                            paramName.forEach(item => {
                                params[item] = value;
                            })
                        }
                        item.dataQuery = JSON.stringify(params);
                    }
                    this.$refs[this.common.NAME + index].forEach(ele => {
                        ele.updateData();
                    });
                });
            }
            if(type === "bar" || type === "line"){
                child.forEach(o => {
                    const item = this.contain.findlist(o);
                    if (item.component.name === "iframe") {
                        if (item.data.value) {
                            const url = item.data.value.split("?");
                                let str = '';
                            if(url.length === 1){
                                for(let k in value){
                                    str += str === '' ? `${k}=${value[k]}` : `&${k}=${value[k]}`
                                }
                                
                            }else{
                                const params = url[1].split("&");
                                params.forEach(p => {
                                    const fs = p.split('=');
                                    if(value[fs[0]]){
                                        str += str === '' ? `${fs[0]}=${value[fs[0]]}` : `&${fs[0]}=${value[fs[0]]}`;
                                    }else{
                                        str += str === '' ? `${fs[0]}=${fs[1]}` : `&${fs[0]}=${fs[1]}`;
                                    }
                                })
                            }
                            item.data.value = `${url[0]}?${str}`
                        }
                    }
                    // else{
                        
                    // }
                    this.$refs[this.common.NAME + o].forEach(ele => {
                        ele.updateData();
                    });
                })
            }
            if(type === "share"){
                const indexList = child.index;
                indexList.forEach(item => {
                    const obj = this.contain.findlist(item);
                    const opts = obj.option
                    const {dataFormatterType, shareGetValueFields, fieldMap} = opts
                    const { dataShare } = this.$store.state
                    if(obj.dataType === 2 && shareGetValueFields){
                        if(dataFormatterType !== 'dataChange'){
                            obj.data = dataShare[shareGetValueFields]
                        }else{
                            obj.data = window.dataToChart(
                                obj.component.name,
                                {data: dataShare[shareGetValueFields]},
                                fieldMap ? JSON.parse(fieldMap) : {},
                                opts
                            )
                        }
                        this.$refs[this.common.NAME + item].forEach(ele => {
                            ele.updateData();
                        });
                    }
                })
            }
        },
        //刷新数据
        handleRefresh(tip = true) {
            this.$refs[this.common.NAME + this.contain.activeObj.index][0].updateData();
            if (tip) {
                this.$message.success("刷新成功");
            }
        },
        //获取对象
        handleGetObj(val) {
            return this.$refs[`${this.common.DEAFNAME}${val}`];
        },
        handleOver({ index }) {
            this.contain.overactive = index;
        },
        handleFocus({ index }) {
            this.container.gradeFlag = true;
            if (this.contain.keys.ctrl) {
                if (!Array.isArray(this.contain.active)) {
                    this.contain.handleInitActive();
                }
                this.contain.active.push(index);
            } else if (!this.contain.active.includes(index)) {
                this.contain.active = [index];
            }
        },
        handleBlur({ left, top, width, height }) {
            if (Array.isArray(this.contain.activeObj)) return;
            this.container.gradeFlag = false;
            this.$set(this.contain.activeObj.component, "width", width);
            this.$set(this.contain.activeObj.component, "height", height);
            this.$set(this.contain.activeObj, "left", left);
            this.$set(this.contain.activeObj, "top", top);
        },
    },
};
</script>

